document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp() {
    crearGaleria();
    scrollNav();
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion__menu a');
    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e){
            e.preventDefault();

            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(e.target.attributes.href.value);
            seccion.scrollIntoView({ behavior: 'smooth' });
        })
    })
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');

    for (let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/galeria/${i}.avif" type="image/avif"> 
            <source srcset="build/img/galeria/${i}.webp" type="image/wepb"> 
            <img loading="lazy" width="350" height="210" src="build/img/galeria/${i}.png" alt="Imagen de la galeria">
        `;

        imagen.onclick = function() {
            mostrarImagen(i);
        }

        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id) {
    const imagen = document.createElement('picture');
    imagen.innerHTML = `
        <source srcset="build/img/galeria/${id}.avif" type="image/avif"> 
        <source srcset="build/img/galeria/${id}.webp" type="image/wepb"> 
        <img loading="lazy" width="850" height="610" src="build/img/galeria/${id}.png" alt="Imagen de la galeria">
    `;
    
    //CREAR OVERLAY CON LA IMAGEN
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    
    overlay.onclick = function() {
        const body = document.querySelector('body');
        body.appendChild(overlay);
        body.classList.remove('fijar-body');
        overlay.remove();
    }

    //AÃ‘ADIR OVERLAY CAL HTML
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');

    //BOTON PARA CERRAR LA IMAGEN
    const cerrar = document.createElement('P');
    cerrar.textContent = 'X';
    cerrar.classList.add('boton-cerrar');

    cerrar.onclick = function() {
        overlay.remove();
    }

    overlay.appendChild(cerrar);
}